import delivery from './delivery.vue';
import orderCartGroup from './orderCartGroup.vue';
import cartItem from './cartItem.vue';
import tableWare from './tableWare.vue';
import { mapGetters } from 'vuex';
import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			haveAddress: false,
			showTableware: false
		};
	},

	onLoad() {
		if (!this.orderInfo) {
			uni.switchTab({
				url: '../tabBar/home/home'
			});
		} else {
			this.initData();
		}
	},
	computed: {
		...mapGetters(['orderInfo', 'totalPrice', 'remarkInfo', 'userInfo'])
	},
	components: {
		delivery,
		orderCartGroup,
		cartItem,
		tableWare
	},
	methods: {
		initData() {
			try {
				const value = uni.getStorageSync('ele_login');
				if (value) {
					this.request({
						url: interfaces.getUserAddress + '/' + value.userId,
						success: res => {
							console.log(res);
							if (res.myAddress.length > 0) {
								this.haveAddress = true;
							} else {
								this.haveAddress = false;
							}
						}
					});
				}
			} catch (e) {
				console.log(e);
			}
		},
		showRemak() {
			uni.navigateTo({
				url: '../remark/remark'
			});
		},
		handelAddress() {
			uni.navigateTo({
				url: '../myAddress/myAddress'
			});
		},
		addAddress() {
			uni.navigateTo({
				url: '../addAddress/addAddress?title=添加地址'
			});
		},
		handlePay() {
			if (!this.userInfo) {
				uni.showToast({
					title: '请选择收货地址'
				})
				return;
			}
			uni.navigateTo({
				url: '../pay/pay'
			})
		}
	}
};
